<template>
  <div>
    <input v-model="loginName"/>
    <input v-model="loginPassWord"/>
    <button v-on:click="dothis">{{msg}}</button>
  </div>
</template>

<script>
  import { loginByUsername } from '@/api/login'
  export default {
    name: 'hello-world',
    data() {
      return {
        loginName: '',
        loginPassWord: '',
        msg: '点击'
      }
    },
    methods: {
      dothis: function() {
        loginByUsername(this.loginName, this.loginPassWord).then(response => {
          const data = response.data
          alert(data)
        })
      }
    }
  }
</script>

<style>

</style>



